import React, { Fragment } from 'react';
import { Button } from 'react-bootstrap';
import { ItemComponent } from '../allJS'

export default function ForCustomerComponent() {
    return (
        <Fragment>
            <div className="row" style={{ height: 800, backgroundColor: '#F8F9FA', marginTop: 40, borderRadius: 10, color: 'black', fontWeight: 'lighter', padding: 10 }}>
                <div className="col">
                    <h5 style={{ color: 'orange' }}><strong>Dành riêng cho bạn</strong></h5>
                    <hr></hr>
                    <div className="row">
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                        <ItemComponent></ItemComponent>
                    </div>
                    <div className="row" style={{ marginTop: 20, alignItems: 'center', justifyContent: 'center' }}>
                        <Button variant="outline-primary">Xem Thêm</Button>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
