import React, { Fragment } from 'react';
import { ListGroup, Button, Form, Row, Col, Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {useCookies} from 'react-cookie';
import {axios} from '../../config/constant';


export default function MainAdmin() {
    const [cookies,setCookies] = useCookies();

    async function KiemTraTokenAdmin(event){
        event.preventDefault();
        await axios.get('hethong/auth/token-admin', {headers:{'token':cookies.token}}).then(function(res){
            if(res.data.status === "fail")
            {             
                alert("Vui lòng đăng nhập tài khoản Admin để sử dựng chức năng này");
            }else
            {
                alert("Sử dụng thành công");
            }
        }).catch(function(err){
            console.log(err);
        });
    }
    return (
        <Fragment>
            <div className="container-fluid" style={{ backgroundColor: 'red', marginTop: '100px' }}>
                <div className="row">
                    <div className="col-sm-2" style={{ height: 800, backgroundColor: '#3399FF', borderRadius: 20 }}>
                        <div style={{ padding: 20, color: 'white' }}>
                            <h4><center>Chức năng Admin</center></h4>
                        </div>
                        <div>
                            <ListGroup>
                                <Link to="/" style={{ textDecoration: 'none' }} onClick={(e)=>{
                                    if(cookies.userID === undefined)
                                    {
                                        e.preventDefault();
                                        alert("Vui lòng đăng nhập để sử dụng chức năng");
                                    }else{
                                        KiemTraTokenAdmin(e);
                                    }
                                }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý Carousel
                                    </ListGroup.Item>
                                </Link>

                                <Link to="/" style={{ textDecoration: 'none' }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý Banner
                                    </ListGroup.Item>
                                </Link>
                                <Link to="/" style={{ textDecoration: 'none' }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý Category
                                    </ListGroup.Item>
                                </Link>
                                <Link to="/" style={{ textDecoration: 'none' }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý Sản Phẩm
                                    </ListGroup.Item>
                                </Link>
                                <Link to="/" style={{ textDecoration: 'none' }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý Shop
                                    </ListGroup.Item>
                                </Link>
                                <Link to="/" style={{ textDecoration: 'none' }}>
                                    <ListGroup.Item style={{ marginTop: 10 }}>
                                        Quản lý User
                                    </ListGroup.Item>
                                </Link>
                            </ListGroup>
                        </div>


                    </div>
                    <div className="col-sm-10" style={{ backgroundColor: 'yellow', padding: 20 }}>
                        <div className="col" style={{ backgroundColor: 'green', width: '100%' }}>
                            <Form>
                                <Row>
                                    <Col>
                                        <Form.Control placeholder="ID, tên Carousel" />
                                    </Col>
                                    <Col>
                                        <Form.Control as="select">
                                            <option>Trạng thái</option>
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                        </Form.Control>
                                    </Col>
                                    <Col>
                                        <Button variant="primary" type="submit" style={{ width: 200 }}>
                                            <i className="fa fa-search"></i> &nbsp; Tìm kiếm
                                        </Button>
                                    </Col>
                                </Row>
                            </Form>
                        </div>
                        <div className="col" style={{width: '100%', marginTop: 20 }}>
                            <Table bordered hover>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>First Name</th>
                                        <th>Last Name</th>
                                        <th>Username</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>1</td>
                                        <td>Mark</td>
                                        <td>Otto</td>
                                        <td>@mdo</td>
                                    </tr>
                                    <tr>
                                        <td>2</td>
                                        <td>Jacob</td>
                                        <td>Thornton</td>
                                        <td>@fat</td>
                                    </tr>
                                    <tr>
                                        <td>3</td>
                                        <td>Larry the Bird</td>
                                        <td>Larry the Bird</td>
                                        <td>@twitter</td>
                                    </tr>
                                </tbody>
                            </Table>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
