import React, { Fragment } from 'react';
import { Form, Button, Row, Col } from 'react-bootstrap';

export default function DangKyComponent() {
    return (
        <Fragment>
            <div className="row">
                <div className="col-sm-4" style={{ height: 600, padding: 40 }}>
                    <h3>Đăng Ký</h3>
                    <span style={{ color: 'grey' }}>Tạo tài khoản để theo dõi đơn hàng, lưu
                                    danh sách sản phẩm yêu thích, nhận nhiều ưu đãi hấp dẫn.</span>
                </div>
                <div className="col-sm-8" style={{ height: 600, padding: 40, borderLeft: 1 }}>
                    <Form>
                        <Form.Group as={Row} controlId="formBasicHoTen">
                            <Form.Label column sm={3}>Họ tên</Form.Label>
                            <Col sm={9}>
                                <Form.Control type="text" placeholder="Nhập họ tên" />
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row} controlId="formBasicSdt">
                            <Form.Label column sm={3}>SĐT</Form.Label>
                            <Col sm={9}>
                                <Form.Control type="text" placeholder="Nhập số điện thoại" />
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row} controlId="formBasicEmail">
                            <Form.Label column sm={3}>Email</Form.Label>
                            <Col sm={9}>
                                <Form.Control type="email" placeholder="Nhập email" />
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row} controlId="formBasicPass">
                            <Form.Label column sm={3}>Mật khẩu</Form.Label>
                            <Col sm={9}>
                                <Form.Control type="password" placeholder="Nhập mật khẩu" />
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row}>
                            <Form.Label column sm={3}>Giới tính</Form.Label>
                            <Col sm={9}>
                                <Row>
                                    <Form.Check type="radio" label="Nam" name="radioGioiTinh" id="formHorizontalRadios1" style={{ marginLeft: 15 }} />
                                    <Form.Check type="radio" label="Nữ" name="radioGioiTinh" id="formHorizontalRadios2" style={{ marginLeft: 100 }} />
                                </Row>
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row} controlId="formBasicPass">
                            <Form.Label column sm={3}>Ngày sinh</Form.Label>
                            <Col sm={9}>
                                <Row>
                                    <Col sm={4}>
                                        <Form.Control as="select" value="Ngày">
                                            <option>Choose...</option>
                                            <option>...</option>
                                        </Form.Control>
                                    </Col>
                                    <Col sm={4}>
                                        <Form.Control as="select" value="Tháng">
                                            <option>Choose...</option>
                                            <option>...</option>
                                        </Form.Control>
                                    </Col>
                                    <Col sm={4}>
                                        <Form.Control as="select" value="Năm">
                                            <option>Choose...</option>
                                            <option>...</option>
                                        </Form.Control>
                                    </Col>
                                </Row>
                            </Col>
                        </Form.Group>
                        <Form.Group as={Row}>
                            <Form.Label column sm={3}></Form.Label>
                            <Col sm={9}>
                                <Button variant="danger" type="submit" block>
                                    Đăng Ký
                                                </Button>
                            </Col>
                        </Form.Group>
                    </Form>
                </div>
            </div>
        </Fragment>
    )
}
