import React, { useState, useEffect } from 'react';
import {MarkettingComponent,DealComponent,HotIndustryComponent,ForCustomerComponent} from '../allJS';
import {axios} from '../../config/constant';

function TrangChu() {
    const [dataCarousel,setDataCarousel] = useState([]);
    const [dataCategoryParent,setDataCategoryParent] = useState([]);
    const [dataBanner,setDataBanner] = useState([]);

    //const [cookies, setCookies] = useCookies();

    async function getDataCarousel(){
        await axios.get('hethong/carousels').then(function(res){
            setDataCarousel(res.data);
        }).catch(function(err){
            console.log(err);
        });
    }

    async function getDataCategoryParent(){
        await axios.get('hethong/category-parent').then(function(res){
            setDataCategoryParent(res.data);
        }).catch(function(err){
            console.log(err);
        });
    }

    async function getDataBanner(){
        await axios.get('hethong/banners').then(function(res){
            setDataBanner(res.data);
        }).catch(function(err){
            console.log(err);
        });
    }

    useEffect(()=>{
        getDataCarousel();
        getDataCategoryParent();
        getDataBanner();
    },[]);


    return (
        <div className="container" style={{ marginTop: '100px' }}>
            <MarkettingComponent dataBanner={dataBanner} dataCategoryParent={dataCategoryParent} dataCarousel={dataCarousel}></MarkettingComponent>
            <DealComponent></DealComponent>
            <HotIndustryComponent dataCategoryParent={dataCategoryParent}></HotIndustryComponent>
            <ForCustomerComponent></ForCustomerComponent>
        </div>
    );
}

export default TrangChu;