import React, { useState, Fragment } from 'react';
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Modal, Tab, Tabs,Badge } from 'react-bootstrap';
import { DangKyComponent, DangNhapComponent, UserComponent } from '../allJS';
import { useSelector, useDispatch } from 'react-redux';
import { useCookies } from 'react-cookie';

function Header() {
    const [showModal, setShowModal] = useState(false);

    const handleCloseModal = () => setShowModal(false);
    const handleShowModal = () => setShowModal(true);

    const trangThaiDangNhap = useSelector(state => state.trangThaiDangNhap);
    const dispatch = useDispatch();

    const [cookies, setCookies] = useCookies();

    return (
        <Fragment>
            <Navbar bg="light" variant="light" fixed="top">
                <Navbar.Brand href="/">
                    <img
                        alt=""
                        src='/logo.png'
                        width="40"
                        height="40"
                        style={{ marginRight: 5 }}
                        className="d-inline-block"
                    />
                    <span style={{ fontWeight: 'bold', color: 'orange' }}>TiemDo</span>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav>
                        <Nav.Link active="true" href="/">Trang Chủ</Nav.Link>
                        <NavDropdown title="Sản Phẩm" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">Thời trang - Phụ kiện</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Điện thoại - Máy tính bảng</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Điện Gia Dụng</NavDropdown.Item>
                            {/* <NavDropdown.Divider /> */}
                            <NavDropdown.Item href="#action/3.4">Laptop - Thiết bị IT</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.4">Máy Ảnh - Quay Phim</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="#link1">Shop Của Bạn</Nav.Link>
                        <Nav.Link href="#link">Liên Hệ</Nav.Link>
                    </Nav>
                    <Form inline style={{ marginLeft: '18%' }}>
                        <FormControl type="text" placeholder="Search" style={{ width: 300 }} className="mr-sm-1" />
                        <Button variant="outline-success">Tìm Kiếm</Button>
                    </Form>
                    <Nav style={{ marginLeft: '22%' }}>
                        <Nav.Link href="#memes1"><Badge variant="primary"><i className="fa fa-shopping-cart" style={{ fontSize: 25 }}></i><strong>0</strong></Badge></Nav.Link>
                        {/* <Nav.Link onClick={handleShowModal} >Đăng Ký / Đăng Nhập</Nav.Link> */}

                        {cookies.token === undefined ?
                            (<Nav.Link onClick={handleShowModal} >Đăng Ký / Đăng Nhập</Nav.Link>) : (<UserComponent></UserComponent>)}

                    </Nav>
                </Navbar.Collapse>
            </Navbar>

            <Modal show={showModal} size="lg" onHide={handleCloseModal} animation={false}>
                <Tabs defaultActiveKey="dangnhap" id="uncontrolled-tab-example">
                    <Tab eventKey="dangnhap" title="Đăng Nhập">
                        <DangNhapComponent></DangNhapComponent>
                    </Tab>
                    <Tab eventKey="dangky" title="Đăng Ký">
                        <DangKyComponent></DangKyComponent>
                    </Tab>
                </Tabs>

            </Modal>


        </Fragment>
    );
}

export default Header;