import React, { Fragment } from 'react';
import './App.css';
import "bootstrap/dist/css/bootstrap.css";
import { Header, Footer, TrangChu, SanPham, SanPhamDetail } from '../allJS';
import MainAdmin from '../Admin/MainAdmin';
import { Switch, Route } from "react-router-dom";

function App() {
  return (
    <Fragment>
      <Header></Header>
      <Switch>
        <Route exact path="/" component={TrangChu}></Route>
        <Route path="/products" component={SanPham}></Route>
        <Route path="/admin" component={MainAdmin}></Route>
        <Route path="/detail" component={SanPhamDetail}></Route>
        <Route component={TrangChu}></Route>
      </Switch>
      <Footer></Footer>
    </Fragment>
  );
}

export default App;
