import React from 'react';

import { ItemComponent,SideBarComponent} from '../allJS';
import { Pagination } from 'react-bootstrap';


export default function SanPham() {
    return (
        <div className="container" style={{ marginTop: '100px' }}>
            <div className="row">
                <SideBarComponent></SideBarComponent>
                <div className="col-sm-9" style={{ backgroundColor: '#' }}>
                    <div className="row maincontent">
                        {/* Ít nhất 20 item */}
                        <div className="row showitems-maincontent">
                            {[...Array(20)].map(()=>{
                                return <ItemComponent></ItemComponent>
                            })}
                        </div>
                        <div className="pagination-maincontent">
                            <Pagination>
                                <Pagination.First />
                                <Pagination.Prev />
                                <Pagination.Item>{1}</Pagination.Item>
                                <Pagination.Ellipsis />

                                <Pagination.Item>{10}</Pagination.Item>
                                <Pagination.Item>{11}</Pagination.Item>
                                <Pagination.Item active>{12}</Pagination.Item>
                                <Pagination.Item>{13}</Pagination.Item>
                                <Pagination.Item disabled>{14}</Pagination.Item>

                                <Pagination.Ellipsis />
                                <Pagination.Item>{20}</Pagination.Item>
                                <Pagination.Next />
                                <Pagination.Last />
                            </Pagination>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
