import React, { useState } from 'react';
import { Image, Button} from 'react-bootstrap';
import { FaCartPlus } from 'react-icons/fa';

export default function InforItemComponent() {

    let srcIMG = ["/logo192.png","/hinhmau.jpg","/logo192.png","/carouselmau.jpg"]

    const [srcHinhLon,setSrcHinhLon] = useState("/hinhmau.jpg")
    return (
        <div className="row">
            <div className="col-sm-5">
                <Image src={srcHinhLon} className="showimg_big"></Image>
                <div className="col">
                    <div className="row" style={{ marginTop: 10 }}>
                        {srcIMG.map((item) => {
                            return <div className="border border-dark showimg_little">
                                <Image src={item} className="img_little" 
                                onClick={()=>{
                                    setSrcHinhLon(item);
                                }}></Image>
                            </div>
                        })}

                    </div>
                </div>
            </div>
            <div className="col-sm-7 infor-item">
                <h3>Giày VANS VAULT STYLE 36 BLACK WHITE</h3>
                <hr></hr>
                <h5 style={{ color: 'red' }}><strong>200.000 VNĐ</strong></h5>
                <p>Khuyến mãi: 50%</p>
                <p>Giá gốc: 400.000 VNĐ</p>
                <hr></hr>
                {[...Array(6)].map(() => {
                    return <p>- Thiết kế kiểu tã quần tiện lợi</p>
                })}
                <hr></hr>
                <div className="row">
                    <div className="col">
                        <p>Số lượng:</p>
                        <input type="number" value="100"></input>
                    </div>
                    <Button style={{ width: 300, marginRight: 100 }}>
                        <div className="row" style={{ justifyContent: 'center', alignItems: 'center' }}>
                            <FaCartPlus size={30}></FaCartPlus>&nbsp;<strong>CHỌN MUA</strong>
                        </div>
                    </Button>
                </div>
            </div>
        </div>
    )
}
